import React, { useState } from "react";
import styled from "styled-components";
import faqIcon from '../../components/img/icon/question-mark-on-a-circular.svg'

const Icon = styled.img`
  padding:0 px;
  width:20px;
  height:20px;
`
const FaqItems = styled.div`
  display:flex;
  padding-bottom:34px;
  border-bottom:1px solid #D49E6A;
   & + & {
    margin-top:20px;
  }
`
const FaqItemDetail = styled.div`
  margin-left:20px;
`
const FaqItemHeader = styled.div`
  cursor:pointer;
`
const FaqItemAns = styled.div`
  margin-top:20px;
`

const faqOptions = [
  {
    question: '',
    answer: `



!

10!




`,
    id: 1,
    isShowed: false
  },
  {
    question: '',
    answer: `Step1. ()
Step2. 
Step3.

Step4.
    (1)   
  (2)  

Step5.

Step6.`,
    id: 2,
    isShowed: false
  },
  {
    question: '',
    answer: ``,
    id: 3,
    isShowed: false
  },
  {
    question: '&',
    answer: `!


  160
         190
  320
         440`,
    id: 4,
    isShowed: false
  },
  {
    question: '?',
    answer: ``,
    id: 5,
    isShowed: false
  },
  {
    question: '??',
    answer: ``,
    id: 6,
    isShowed: false
  }
]


const RenderFaqItems = () => {
  const [selectedOption, setSelectedOption] = useState(faqOptions)

  const handleQuestionClick = (selectedItem) => {
    setSelectedOption(selectedOption.map(item => {
      if (selectedItem.id === item.id) {

        const { isShowed } = item
        return {
          ...item,
          isShowed: !isShowed
        }
      }
      return item
    }))
  }

  return selectedOption.map(item => {
    console.log(item.isShowed, item.id)
    return (
      <FaqItems key={item.id}>
        <div>
          <Icon src={faqIcon} />
        </div>
        <FaqItemDetail>
          <FaqItemHeader onClick={() => handleQuestionClick(item)}> {item.question} </FaqItemHeader>
          {item.isShowed && <FaqItemAns> {item.answer} </FaqItemAns>}
        </FaqItemDetail>
        {console.log('====')}

      </FaqItems>
    )
  })
}

export default RenderFaqItems